import { readMessage as _readMessage, deleteMessage as _deleteMessage, acceptInvitation, refuseInvitation } from '../api/api';
export default {
  name: 'messageCard',
  props: {
    messages: {
      type: Array,
      required: true
    }
  },
  methods: {
    //单个消息已读
    readMessage: function readMessage(messageId, messageStatus) {
      if (messageStatus === 1) messageStatus = 0;else if (messageStatus === 0) messageStatus = 1;

      _readMessage(messageId, messageStatus).then(function (response) {
        if (response.status === 200) {
          console.log(response);
        } else {
          console.log(response);
        }
      });

      this.$emit('getChangeFromSon');
    },
    //单个消息删除
    deleteMessage: function deleteMessage(messageId) {
      _deleteMessage(messageId).then(function (response) {
        if (response.status === 200) {
          console.log(response);
        } else {
          console.log(response);
        }
      });

      this.$emit('getChangeFromSon');
    },
    //查看详细信息
    viewMessage: function viewMessage(item) {
      var _this = this;

      item.status = 1;

      if (item.type === 1) {
        //1:团队邀请信息（给被邀请人发） 接受或拒绝
        this.$confirm('是否接受邀请加入该团队？', '团队邀请', {
          confirmButtonText: '接受',
          cancelButtonText: '拒绝',
          type: 'info'
        }).then(function () {
          acceptInvitation(item.document.id, item.user.id).then(function (response) {
            if (response.status === 200) {
              _this.$message({
                type: 'success',
                message: '成功加入团队'
              });
            }
          }).catch(function () {
            _this.$message({
              type: 'error',
              message: '加入团队失败'
            });
          });
        }).catch(function () {
          refuseInvitation(item.document.id, item.user.id).then(function (response) {
            if (response.status === 200) {
              _this.$message({
                type: 'success',
                message: '成功拒绝该团队邀请'
              });
            }
          }).catch(function () {
            _this.$message({
              type: 'error',
              message: '拒绝团队邀请失败'
            });
          });
        });
      } else if (item.type === 2) {
        //2:加入团队结果（给邀请人+老大发）
        this.$router.push({
          name: 'myTeam'
        });
      } else if (item.type === 3) {
        //3:退出团队结果（给老大发）
        this.$message({
          message: item.origin_user.username + '已退出团队',
          type: 'info'
        });
      } else if (item.type === 4) {
        //4:被踢出团队提醒（给被踢的人发）
        this.$message({
          message: '你已被移出团队[' + item.document.name + ']',
          type: 'info'
        });
      } else if (item.type === 5) {
        //5:被加入协作者提醒（给被邀请人发）
        this.$router.push({
          name: 'editorPage',
          params: {
            docId: item.document.id
          }
        });
      } else if (item.type === 6) {
        //6:文档被评论（给被评论的文档的创建者+协作者发）
        this.$router.push({
          name: 'editorPage',
          params: {
            docId: item.document.id
          }
        });
      } else if (item.type === 7) {
        //7:评论被回复（给被回复的评论人发）
        this.$router.push({
          name: 'editorPage',
          params: {
            docId: item.document.id
          }
        });
      } else if (item.type === 8) {
        //8:退出文档协作者结果（给创建者发）
        this.$router.push({
          name: 'editorPage',
          params: {
            docId: item.document.id
          }
        });
      } else if (item.type === 9) {
        //9：被踢出文档协作者提醒（给被踢的人发）
        this.$message({
          message: '你已被移出文档[' + item.document.name + ']协作者',
          type: 'info'
        });
      } else if (item.type === 10) {
        //10: 拒绝团队邀请（给老大发）
        this.$message({
          message: item.origin_user.username + '拒绝加入团队[' + item.document.name + ']',
          type: 'info'
        });
      }
    }
  }
};